body {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80vh;
}
button {
  background: #3F51B5;
  color: #fff;
  font-size: 18px;
  border: 1px solid #3f51b5;
  padding: 8px 15px;
  cursor: pointer;
}
button:hover {
  background: #26389c;
  border: 1px solid #26389c;
}
button:focus, input {
  outline: none;
}
input, .countlabel {
  font-size: 18px;
  border: 1px solid #3f51b5;
  padding: 8px 15px;
  width: 30px;
  text-align: center;
  font-family: sans-serif;
  display: inline-block;
}
.reset {
  margin-top: 30px;
  font-size: 14px;
  padding: 10px 35px;
}
.note {
  font-family: sans-serif;
  font-size: 12px;
  font-style: italic; 
}